import React, {useContext} from "react";
import s from "./NeuronLearningPublicItem.module.css"
import {NeuronPublicType} from "../../types/types";
import {CustomChip} from "../customChip/CustomChip";
import {rainbowColorEnum} from "../../static/static";
import {PriceItemMain} from "../priceItemMain/PriceItemMain";
import {Button} from "../button/Button";
import {AppDataContext} from "../../context/AppDataContext";
import {NeuronLearningDurationTime} from "../neuronLearningDurationTime/NeuronLearningDurationTime";
import {SignalInfoItemVolume} from "../signalInfoItemVolume/SignalInfoItemVolume";
import {InfoItemMain} from "../infoItemMain/InfoItemMain";

type propsType = {
  neuron: NeuronPublicType
}

const NeuronLearningPublicItem: React.FC<propsType> = props => {

  const {
    neuron
  } = props

  const value = useContext(AppDataContext);
  if (!value) return null

  const detailHandler = (neuron_uuid?: string) => {
    if (!neuron_uuid) {
      return
    }
    value.getNeuronLearningFullByUUID(neuron_uuid)
  }

  const goToParentNeuron = (neuron_uuid?: string) => {
    if (!neuron_uuid) {
      return
    }
    value.goToParentNeuron(neuron_uuid);
    setTimeout(() => {
      value.goToParentNeuron(undefined);
    }, 2500);
  }

  return (
    <div
      className={`${s.wrapper} ${value.neuronParentUUIDHighlighted === neuron.neuron_uuid && s.highlightedWrapper}`}
    >
      <div className={s.ceilString}>
        {neuron.neuron_tag_name && <InfoItemMain
          title={neuron.neuron_tag_name}
        />}
      </div>

      <div>
        {neuron.neuron_is_current && <CustomChip
            label="Current"
            background={rainbowColorEnum.dark_green}
            color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        <PriceItemMain
          label="EPOCHS:"
          price_enter={neuron.neuron_epochs}
        />
      </div>

      <div>
        <PriceItemMain
          label="RATE:"
          price_enter={neuron.neuron_learning_rate}
        />
      </div>

      <div className={s.dateString}>
        {new Date(neuron.created_at).toLocaleString()}
      </div>

      <div>
        {neuron.neuron_uuid}
      </div>

      <div>
      </div>

      <div>
        <PriceItemMain
          label="LIMIT:"
          price_enter={neuron.neuron_limit}
        />
      </div>

      <div>
        <PriceItemMain
          label="OFFSET:"
          price_enter={neuron.neuron_offset}
        />
      </div>

      <div>
        <NeuronLearningDurationTime
          created_at={neuron.created_at}
          finish_at={neuron.finish_at}
        />
      </div>

      <div className={s.ceilButtons}>
        {neuron.neuron_parent_uuid && <Button
          clickHandler={() => detailHandler(neuron.neuron_parent_uuid)}
          label={neuron.neuron_parent_uuid}
        />}
        {neuron.neuron_parent_uuid && <Button
          clickHandler={() => goToParentNeuron(neuron.neuron_parent_uuid)}
          label="link"
        />}
      </div>

      <div>
      </div>

      <div>
        {neuron.neuron_loss_train_last && <SignalInfoItemVolume
            label="TRAIN LOSS:"
            value="%"
            relativeValue={(neuron.neuron_loss_train_last).toFixed(2)}
        />}
      </div>

      <div>
        {neuron.neuron_loss_real_last && <SignalInfoItemVolume
          label="REAL LOSS:"
          value="%"
          relativeValue={(neuron.neuron_loss_real_last).toFixed(2)}
        />}
      </div>

      <div className={s.dateString}>
        <Button
         clickHandler={() => detailHandler(neuron.neuron_uuid)}
         label="Detail"
         disabled={!neuron.finish_at}
        />
      </div>

    </div>
  )
}

export {NeuronLearningPublicItem}
