import React, {useContext} from "react";
import s from "./signalApprovedHandler.module.css"
import {AppDataContext} from "../../context/AppDataContext";
import {rainbowColorEnum} from "../../static/static";
import { Button } from "../button/Button";

type propsType = {
  signal_is_approved: boolean
}

const SignalApprovedHandler: React.FC<propsType> = props => {

  const {
    signal_is_approved
  } = props

  const value = useContext(AppDataContext);

  if (!value) {
    return null
  }
  if (!value.signalFull) {
    return null
  }

  const changeApprovedValue = async () => {
    value.changeApprovedValueForSignalTrade()
  }

  return (
    <div className={s.flexRow}>
      <Button
        label={signal_is_approved ? "Set Not Approved" : "Set Approved"}
        clickHandler={changeApprovedValue}
        background={rainbowColorEnum.dark}
        color={signal_is_approved ? rainbowColorEnum.red : rainbowColorEnum.green}
        width={142}
      />
    </div>
  )
}

export {SignalApprovedHandler}
