import React, {useContext} from "react";
import s from "./signalTraderPublicItem.module.css"
import {
  SignalPublicWithSymbolPublicPlatformPublicNeuronMainTradePublicType,
  SignalReportWithSymbolPublicPlatformPublicNeuronMainTradePublicStatusType
} from "../../types/types";
import {CustomChip} from "../customChip/CustomChip";
import {rainbowColorDict, rainbowColorEnum} from "../../static/static";
import {getColorDirection} from "../../utils/utils";
import {InfoItemMain} from "../infoItemMain/InfoItemMain";
import {PriceItemMain} from "../priceItemMain/PriceItemMain";
import {Button} from "../button/Button";
import {AppDataContext} from "../../context/AppDataContext";
import {SignalDirectionDisplayComplex} from "../signalDirectionDisplayComplex/SignalDirectionDisplayComplex";
import {PriceItemNew} from "../priceItemNew/PriceItemNew";
import {SignalReportHandler} from "../signalReportHandler/SignalReportHandler";

type propsType = {
  signal: SignalReportWithSymbolPublicPlatformPublicNeuronMainTradePublicStatusType
}

const SignalTraderPublicItem: React.FC<propsType> = props => {

  const {
    signal
  } = props

  const value = useContext(AppDataContext);
  if (!value) return null

  const detailHandler = () => {
    value.getSignalFullByUUID(signal.signal_uuid)
    value.getSignalTradeByUUID(signal.signal_uuid)
  }

  const setSignalActiveSearchElement = (val: string) => {
    value.setSignalActiveSearchElement(val)
  }

  return (
    <div
      className={s.wrapper}
      style={{
        borderLeft: `16px solid ${rainbowColorDict[getColorDirection(
          signal.signal_direction_updated ? signal.signal_direction_updated : signal.signal_direction_created
        )]}`
      }}
    >
      <div className={s.ceilString}>
        <InfoItemMain
          title={signal.symbol.symbol_pair}
          titleCallBack={setSignalActiveSearchElement}
        />
        <SignalDirectionDisplayComplex
          signal_direction_created={signal.signal_direction_created}
          signal_direction_updated={signal.signal_direction_updated}
        />
      </div>

      <div>
        {signal.signal_is_manual && <CustomChip
          label="Manual"
          background={rainbowColorEnum.dark_blue}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        {signal.updated_at && <CustomChip
          label="Watched"
          background={rainbowColorEnum.blue}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        {signal.signal_for_learn && <CustomChip
          label="For Learn"
          background={rainbowColorEnum.purple}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        <PriceItemMain
          label="PRICE:"
          price_enter={signal.signal_price_enter}
          percent_close={signal.result_percent_close}
        />
      </div>

      <div className={s.dateString}>
        {new Date(signal.created_at).toLocaleString()}
      </div>


      <div>
        <CustomChip
          label={signal.platform.platform_name}
          background={signal.platform.platform_background}
          color={signal.platform.platform_color}
        />
      </div>

      <div>
        {signal.signal_is_neuron_network && <CustomChip
          label="Neuron"
          background={rainbowColorEnum.dark_green}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        {signal.signal_is_excluded && <CustomChip
          label="Excluded"
          background={rainbowColorEnum.dark_red}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        {signal.signal_is_approved && <CustomChip
          label="Approved"
          background={rainbowColorEnum.violet}
          color={rainbowColorEnum.light}
        />}
      </div>

      <div>
        <PriceItemMain
          label="SCORE:"
          price_enter={signal.signal_score}
        />
      </div>

      <div className={s.dateString}>
        <Button
          clickHandler={detailHandler}
          label="Detail"
        />
      </div>


      <div>
        <PriceItemNew
          label="TRADE LEVERAGE:"
          price_enter={signal.trade.trade_leverage}
        />
      </div>

      <div>
        <PriceItemNew
          label="SL:"
          price_enter={signal.trade.trade_stop_loss_percent.toFixed(2)}
          is_percent
        />
      </div>

      <div>
        <PriceItemNew
          label="TSI:"
          price_enter={signal.trade.trade_treiling_stop_percent_init.toFixed(2)}
          is_percent
        />
      </div>

      <div>
        <PriceItemNew
          label="TSC:"
          price_enter={signal.trade.trade_treiling_stop_percent_current.toFixed(2)}
          is_percent
        />
      </div>

      <div>
        <PriceItemNew
          label="TP:"
          price_enter={signal.trade.trade_take_profit_percent.toFixed(2)}
          is_percent
        />
      </div>

      <div>
        <PriceItemNew
          label="ROE:"
          price_enter={signal.trade.trade_roe_current.toFixed(2)}
          is_percent
        />
      </div>


      <div>
        <PriceItemNew
          label="STATUS:"
          price_enter={signal.trade.trade_status}
        />
      </div>

      <div>
        <PriceItemNew
          label="Duration:"
          price_enter={(((signal.trade.trade_close_timestamp || 0) - signal.trade.trade_create_timestamp) / 60000).toFixed(0)}
        />
      </div>

      <div></div>
      <div></div>
      <div></div>
      <div></div>

      <div>
        <PriceItemNew
          label="REPORT:"
          price_enter={signal.signal_status_report}
        />
      </div>

      <div>
        <PriceItemNew
          label="CLOSE:"
          price_enter={signal.signal_status_close_price || "No"}
        />
      </div>

      <div>
        <PriceItemNew
          label="Duration:"
          price_enter={signal.signal_status_trade_duration
            ? (signal.signal_status_trade_duration / 60000).toFixed(0)
            : "No"
          }
        />
      </div>

      <div>
        <PriceItemNew
          label="ROE:"
          price_enter={signal.signal_status_roe_current
            ? signal.signal_status_roe_current.toFixed(2)
            : "No"
          }
          is_percent={!!signal.signal_status_roe_current}
        />
      </div>

      <div>
        <PriceItemNew
          label="ROE2:"
          price_enter={signal.signal_status_percent_close
            ? signal.signal_status_percent_close.toFixed(2)
            : "No"
          }
          is_percent={!!signal.signal_status_percent_close}
        />
      </div>

      <div className={s.dateString}>
        <SignalReportHandler
          signal_uuid={signal.signal_uuid}
          signal_status_report={signal.signal_status_report}
          signal_is_approved={signal.signal_is_approved}
        />
      </div>
    </div>
  )
}

export {SignalTraderPublicItem}
