import {Link, useLocation} from "react-router-dom"
import s from "./subMenu.module.css"
import {useContext} from "react";
import {AppDataContext} from "../../context/AppDataContext";
import {mainMenuList} from "../../static/static";

const SubMenu = () => {

  const location = useLocation()
  const value = useContext(AppDataContext);

  const menu_item = mainMenuList
    .filter(item => Number(item.is_private) <= Number(value?.isAuth))
    .map(item =>
      <Link
        key={item.label}
        className={`${s.link} ${item.path === location.pathname && s.link_active}`}
        to={item.path}
      >
        {item.label}
      </Link>
    )

  return (
    <div className={s.wrapper}>
      <div className={s.body}>
        <div className={s.submenu}>
          {menu_item}
        </div>
        <div className={s.link}>
          v.0.0.21
        </div>
      </div>
    </div>
  )
}

export {SubMenu}