import {useContext} from "react";
import cs from "./styles.module.css";
import {AppDataContext} from "../context/AppDataContext";
import {SignalTraderPublicItem} from "../components/signalTraderPublicItem/SignalTraderPublicItem";

const SignalsTrader = () => {
  const value = useContext(AppDataContext);

  const signals = value?.signalTraderPublicList.map(item =>
    <SignalTraderPublicItem signal={item} key={item.signal_uuid}/>
  )

  return (
    <div className={cs.list}>
      {signals}
    </div>
  )
}

export {SignalsTrader}
