import React, {ChangeEvent, useContext, useState} from "react";
import s from "./NeuronTagEditor.module.css";
import {InfoItemMain} from "../infoItemMain/InfoItemMain";
import {Button} from "../button/Button";
import {rainbowColorEnum} from "../../static/static";
import {adminApi} from "../../api/api";
import {AppDataContext} from "../../context/AppDataContext";

type PropsType = {
  neuron_uuid: string
  neuron_tag_name?: string
}

const NeuronTagEditor: React.FC<PropsType> = props => {
  const {
    neuron_uuid,
    neuron_tag_name,
  } = props

  const [isLoading, setIsloading] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [draftTagName, setDraftName] = useState<string | undefined>(undefined);

  const value = useContext(AppDataContext);
  if (!value) {
    return null
  }

  const changeDraftTagName = (e: ChangeEvent<HTMLInputElement>) => {
    setDraftName(e.target.value)
  }

  const openEditDraftTagName = () => {
    setDraftName(neuron_tag_name);
    setIsEdit(true);
  }

  const saveEditDraftTagName = async () => {
    setDraftName(undefined);
    setIsEdit(false);
    setIsloading(true);

    await adminApi.changeTagNameValueForNeuronLearningByUUID(
      neuron_uuid,
      draftTagName,
    )

    setIsloading(false);

    value.closeNeuronLearningFullModal();
    value.getNeuronLearningPublicList();
  }

  const cancelEditDraftTagName = () => {
    setDraftName(undefined);
    setIsEdit(false);
  }

  if (isEdit) {
    return (
      <div className={s.flexRow}>
        <div className={s.menuInput}>
          <span>New tag: </span>
          <input
            style={{width: "120px"}}
            type="text"
            value={draftTagName}
            onChange={changeDraftTagName}
          />
        </div>

        <Button
          clickHandler={saveEditDraftTagName}
          label="Save"
          color={rainbowColorEnum.dark_green}
          loaded={isLoading}
        />

        <Button
          clickHandler={cancelEditDraftTagName}
          label="Cancel"
          color={rainbowColorEnum.dark_red}
          loaded={isLoading}
        />

      </div>
    )
  }

  return (
    <div className={s.flexRow}>
      {neuron_tag_name && <InfoItemMain
        title={neuron_tag_name}
      />}

      <Button
        clickHandler={openEditDraftTagName}
        label="Edit Tag"
        loaded={isLoading}
      />
    </div>
  )
}

export { NeuronTagEditor }
