import React, {useContext} from "react";
import s from "./signalFullItemModal.module.css"
import ReactDOM from 'react-dom'
import {AppDataContext} from "../../context/AppDataContext";
import {Button} from "../button/Button";
import {
  rainbowColorEnum
} from "../../static/static";
import {InfoItemMain} from "../infoItemMain/InfoItemMain";
import {CustomChip} from "../customChip/CustomChip";
import {SignalApprovedHandler} from "../signalApprovedHandler/SignalApprovedHandler";
import { Charter } from "../chartComponent/ChartComponent";
import { GridPriceList } from "../gridPriceList/GridPriceList";
import {SignalInfo} from "../signalInfo/SignalInfo";
import {SignalTradeInfo} from "../signalTradeInfo/SignalTradeInfo";
import { SignalDirectionHandler } from "../signalDirectionHandler/SignalDirectionHandler";
import {SignalExcludedHandler} from "../signalExcludedHandler/SignalExcludedHandler";
import { SignalDirectionDisplayComplex } from "../signalDirectionDisplayComplex/SignalDirectionDisplayComplex";

const SignalFullItemModal: React.FC = () => {

  const value = useContext(AppDataContext);
  if (!value) {
    return null
  }
  if (!value.signalFull) {
    return null
  }
  if (!value.signalTradeTrendData) {
    return null
  }

  const signalFull = value.signalFull
  const signalTradeTrendData = value.signalTradeTrendData

  const removeHandler = () => {
    value.removeSignal(signalFull.signal_uuid)
  }

  const openRandomSignalHandler = () => {
    value.openRandomSignalHandler()
  }

  const detailHandler = () => {
    value.getSignalTradeByUUID(signalFull.signal_uuid)
  }

  const closeModal = () => {
    value.closeSignalFullModal()
  }

  const createManualSignal = (delta_timestamp: number) => {
    value.createManualSignal(signalFull.signal_uuid, delta_timestamp)
  }

  return ReactDOM.createPortal(
    <>
      <div className={s.modalBackground} onClick={closeModal}/>
      <div className={s.modalContainer}>
        <div className={s.modalHeader}>
          <div className={s.flexRow}>
            <InfoItemMain
              title={signalFull.symbol.symbol_pair}
            />

            <SignalDirectionDisplayComplex
              signal_direction_created={signalFull.signal_direction_created}
              signal_direction_updated={signalFull.signal_direction_updated}
            />

            <CustomChip
              label={signalFull.platform.platform_name}
              background={signalFull.platform.platform_background}
              color={signalFull.platform.platform_color}
            />

            {signalFull.neuron && <CustomChip
                label={signalFull.neuron.neuron_tag_name ? signalFull.neuron.neuron_tag_name : signalFull.neuron.neuron_uuid}
                background={signalFull.neuron.neuron_is_current ? rainbowColorEnum.dark_green: rainbowColorEnum.gray_2}
                color={rainbowColorEnum.light}
            />}
          </div>
          <div className={s.flexRow}>
            <div>
              {new Date(signalFull.created_at).toLocaleString()}
            </div>
            <Button
              label="Open Random Signal"
              clickHandler={openRandomSignalHandler}
              background={rainbowColorEnum.dark}
              color={rainbowColorEnum.yellow}
            />
            <Button
                label="Remove"
                clickHandler={removeHandler}
                background={rainbowColorEnum.dark}
                color={rainbowColorEnum.dark_red}
            />
            <Button
              label="Reload"
              clickHandler={detailHandler}
              background={rainbowColorEnum.dark}
              color={rainbowColorEnum.orange}
            />
            <Button
              label="Close"
              clickHandler={closeModal}
              background={rainbowColorEnum.dark}
              color={rainbowColorEnum.red}
            />
          </div>
        </div>
        <div className={s.modalHeader}>
          <div className={s.flexRow}>
            {signalFull.signal_for_learn && <CustomChip
                label="For Learn"
                background={rainbowColorEnum.purple}
                color={rainbowColorEnum.light}
            />}

            <CustomChip
              label={signalFull.signal_is_approved ? "Approved" : "Not Approved"}
              background={signalFull.signal_is_approved ? rainbowColorEnum.violet : rainbowColorEnum.gray_1}
              color={rainbowColorEnum.light}
            />

            {signalFull.updated_at && <CustomChip
                label="Watched"
                background={rainbowColorEnum.blue}
                color={rainbowColorEnum.light}
            />}

            {signalFull.signal_is_excluded && <CustomChip
                label="Excluded"
                background={rainbowColorEnum.dark_red}
                color={rainbowColorEnum.light}
            />}

            {signalFull.signal_is_manual && <CustomChip
                label="Manual"
                background={rainbowColorEnum.dark_blue}
                color={rainbowColorEnum.light}
            />}

            {signalFull.signal_is_neuron_network && <CustomChip
                label="Neuron"
                background={rainbowColorEnum.dark_green}
                color={rainbowColorEnum.light}
            />}

          </div>

          <div className={s.flexRow}>
            <SignalDirectionHandler
              signal_direction={signalFull.signal_direction_updated
                ? signalFull.signal_direction_updated
                : signalFull.signal_direction_created
              }
            />
            <SignalApprovedHandler
              signal_is_approved={signalFull.signal_is_approved}
            />
            <SignalExcludedHandler
              signal_is_excluded={signalFull.signal_is_excluded}
            />
          </div>
        </div>

        <div className={s.modalBody}>
          <div className={s.tableBLock}>
            <div>
              <Charter
                dataPrice={signalFull.parsed_price_dataset}
                dataVolume={signalFull.parsed_volume_dataset}
                minPrice={signalFull.parsed_min_price}
                maxPrice={signalFull.parsed_max_price}
                directionManualSignal="past"
                createManualSignal={createManualSignal}
              />
              <GridPriceList
                grid_price_list={signalFull.parsed_grid_price_list}
                direction={signalFull.signal_direction_created}
              />
              <SignalInfo
                parsed_avg_price_300m={signalFull.parsed_avg_price_300m}
                parsed_avg_price_60m={signalFull.parsed_avg_price_60m}
                parsed_avg_price_15m={signalFull.parsed_avg_price_15m}

                parsed_relative_price_300m={signalFull.parsed_relative_price_300m}
                parsed_relative_price_60m={signalFull.parsed_relative_price_60m}
                parsed_relative_price_15m={signalFull.parsed_relative_price_15m}

                parsed_relative_volume_60m={signalFull.parsed_relative_volume_60m}
                parsed_relative_volume_15m={signalFull.parsed_relative_volume_15m}
                parsed_relative_volume_5m={signalFull.parsed_relative_volume_5m}

                parsed_relative_volume_5m_1m={signalFull.parsed_relative_volume_5m_1m}
                parsed_relative_volume_5m_2m={signalFull.parsed_relative_volume_5m_2m}
                parsed_relative_volume_5m_3m={signalFull.parsed_relative_volume_5m_3m}
                parsed_relative_volume_5m_4m={signalFull.parsed_relative_volume_5m_4m}

                global_current_price_btc={signalFull.global_current_price_btc}
                global_trend_fast={signalFull.global_trend_fast}
                global_trend_medium={signalFull.global_trend_medium}
                global_trend_slow={signalFull.global_trend_slow}
                global_trend_ha={signalFull.global_trend_ha}
              />
            </div>
            <div>
              <Charter
                dataPrice={signalTradeTrendData.price_dataset}
                dataVolume={signalTradeTrendData.volume_dataset}
                minPrice={signalTradeTrendData.min_price}
                maxPrice={signalTradeTrendData.max_price}
                directionManualSignal="future"
                createManualSignal={createManualSignal}
              />
              <SignalTradeInfo
                signal_open_price={signalFull.parsed_open_price}
                signal_close_price={signalFull.parsed_close_price}

                signal_min_price={signalFull.parsed_min_price}
                signal_max_price={signalFull.parsed_max_price}

                signal_delta_to_min_price={signalFull.parsed_delta_to_min_price}
                signal_delta_to_max_price={signalFull.parsed_delta_to_max_price}

                signal_delta_price_5m={signalFull.parsed_delta_price_5m}

                signal_score = {signalFull.signal_score}


                trade_open_price={signalTradeTrendData.open_price}
                trade_close_price={signalTradeTrendData.close_price}

                trade_min_price={signalTradeTrendData.min_price}
                trade_max_price={signalTradeTrendData.max_price}

                trade_delta_to_min_price={signalTradeTrendData.delta_to_min_price}
                trade_delta_to_max_price={signalTradeTrendData.delta_to_max_price}

                trade_absolute_value_first={signalTradeTrendData.absolute_value_first}

                result_price_close={signalTradeTrendData.result_price_close}
                result_direction_percent_close={signalTradeTrendData.result_direction_percent_close}
                result_system_message={signalTradeTrendData.result_system_message}
              />
            </div>
          </div>
        </div>
      </div>
    </>,
    document.body,
  );
}

export {SignalFullItemModal}
