import {useContext} from "react";
import cs from "./styles.module.css";
import {AppDataContext} from "../context/AppDataContext";
import {SignalPublicItem} from "../components/signalPublicItem/SignalPublicItem"

const Signals = () => {
  const value = useContext(AppDataContext);

  const signals = value?.signalPublicList.map(item =>
    <SignalPublicItem signal={item} key={item.signal_uuid}/>
  )

  return (
    <div className={cs.list}>
      {signals}
    </div>
  )
}

export {Signals}
