import React from "react";
import s from "./NeuronLearningTrainInfo.module.css"
import {SignalInfoItemVolume} from "../signalInfoItemVolume/SignalInfoItemVolume";


type propsType = {
  neuron_epochs: number
  neuron_learning_rate: number
  neuron_limit: number
  neuron_offset: number

  neuron_item_count_train_buy?: number
  neuron_item_count_train_sell?: number
  neuron_item_count_train_flet?: number
  neuron_item_count_train_total?: number

  neuron_loss_train_last?: number
  neuron_correct_train_last?: number
}

const NeuronLearningTrainInfo: React.FC<propsType> = props => {

  const {
    neuron_epochs,
    neuron_learning_rate,
    neuron_limit,
    neuron_offset,

    neuron_item_count_train_buy,
    neuron_item_count_train_sell,
    neuron_item_count_train_flet,
    neuron_item_count_train_total,

    neuron_loss_train_last,
    neuron_correct_train_last,
  } = props

  return (
    <div className={s.container}>
      <SignalInfoItemVolume
        label="EPOCHS"
        relativeValue={neuron_epochs}
      />
      <SignalInfoItemVolume
        label="RATE"
        relativeValue={neuron_learning_rate}
      />

      <SignalInfoItemVolume
        label="COUNT BUY"
        relativeValue={neuron_item_count_train_buy}
      />
      <SignalInfoItemVolume
        label="COUNT SELL"
        relativeValue={neuron_item_count_train_sell}
      />
      <SignalInfoItemVolume
        label="COUNT FLET"
        relativeValue={neuron_item_count_train_flet}
      />
      <SignalInfoItemVolume
        label="COUNT TOTAL"
        relativeValue={neuron_item_count_train_total}
      />

      <SignalInfoItemVolume
        label="LIMIT"
        relativeValue={neuron_limit}
      />
      <SignalInfoItemVolume
        label="OFFSET"
        relativeValue={neuron_offset}
      />

      <SignalInfoItemVolume
        label="TRAIN LOSS:"
        value="%"
        relativeValue={Number(neuron_loss_train_last).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="TRAIN CORRECT:"
        relativeValue={Number(neuron_correct_train_last)}
      />

      <div></div>
      <div></div>
    </div>
  )
}

export {NeuronLearningTrainInfo}
