import React from "react";
import s from "./priceItemMain.module.css"
import {rainbowColorDict, rainbowColorEnum} from "../../static/static";

type propsType = {
  label: string
  price_enter: number
  percent_close?: number
}

const PriceItemMain: React.FC<propsType> = props => {

  const {
    label,
    price_enter,
    percent_close,
  } = props

  return (
    <div className={s.infoMain}>
      <div className={s.infoPrefix}>
        {label}
      </div>
      <div className={s.infoGroup}>
        <div className={s.infoTitle}>
          {price_enter}
        </div>
        {!!percent_close && <div
            className={s.infoPercent}
            style={{
              color: `${rainbowColorDict[Number(percent_close) > 0
                ? rainbowColorEnum.dark_green
                : rainbowColorEnum.dark_red]}`
            }}
        >
          {percent_close}%
        </div>}
      </div>
    </div>
  )
}

export {PriceItemMain}
