import React from "react";
import s from "./signalTradeInfo.module.css"
import {SignalInfoItemVolume} from "../signalInfoItemVolume/SignalInfoItemVolume";
import {ResultSystemMessageEnum} from "../../types/types";

type propsType = {
  signal_open_price: number,
  signal_close_price: number,

  signal_min_price: number,
  signal_max_price: number,

  signal_delta_to_min_price: number,
  signal_delta_to_max_price: number,

  signal_delta_price_5m: number,

  signal_score: number,


  trade_open_price: number,
  trade_close_price: number,

  trade_min_price: number,
  trade_max_price: number,

  trade_delta_to_min_price: number,
  trade_delta_to_max_price: number,

  trade_absolute_value_first: string, //TODO enum

  result_price_close: number,
  result_direction_percent_close: number,
  result_system_message: ResultSystemMessageEnum,
}

const SignalTradeInfo: React.FC<propsType> = props => {

  const {
    signal_open_price,
    signal_close_price,

    signal_min_price,
    signal_max_price,

    signal_delta_to_min_price,
    signal_delta_to_max_price,

    signal_delta_price_5m,

    signal_score,


    trade_open_price,
    trade_close_price,

    trade_min_price,
    trade_max_price,

    trade_delta_to_min_price,
    trade_delta_to_max_price,

    trade_absolute_value_first,

    result_price_close,
    result_direction_percent_close,
    result_system_message,
  } = props

  return (
    <div className={s.container}>
      <div className={s.title}>Signal Period Information</div>
      <SignalInfoItemVolume
        label="PRICE START"
        relativeValue={signal_open_price}
      />
      <SignalInfoItemVolume
        label="PRICE FINISH"
        relativeValue={signal_close_price}
      />
      <SignalInfoItemVolume
        label="PRICE MAX"
        relativeValue={signal_max_price}
      />
      <SignalInfoItemVolume
        label="PRICE MIN"
        relativeValue={signal_min_price}
      />
      <SignalInfoItemVolume
        label="PER DELTA MAX"
        value="%"
        relativeValue={(signal_delta_to_max_price*100).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="PER DELTA MIN"
        value="%"
        relativeValue={(signal_delta_to_min_price*100).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="PER DELTA 5M"
        value="%"
        relativeValue={(signal_delta_price_5m*100).toFixed(2)}
      />

      <div className={s.title}>Trade Period Information</div>
      <SignalInfoItemVolume
        label="PRICE START"
        relativeValue={trade_open_price}
      />
      <SignalInfoItemVolume
        label="PRICE FINISH"
        relativeValue={trade_close_price}
      />
      <SignalInfoItemVolume
        label="PRICE MAX"
        relativeValue={trade_max_price}
      />
      <SignalInfoItemVolume
        label="PRICE MIN"
        relativeValue={trade_min_price}
      />
      <SignalInfoItemVolume
        label="PER DELTA MAX"
        value="%"
        relativeValue={(trade_delta_to_max_price*100).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="PER DELTA MIN"
        value="%"
        relativeValue={(trade_delta_to_min_price*100).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="ABS V F"
        relativeValue={trade_absolute_value_first}
      />

      <div className={s.title}>Result Trade Information</div>
      <SignalInfoItemVolume
        label="SIGNAL SCORE"
        relativeValue={signal_score}
      />
      <SignalInfoItemVolume
        label="PRICE RESULT"
        relativeValue={result_price_close}
      />
      <SignalInfoItemVolume
        label="PER RESULT"
        value="%"
        relativeValue={result_direction_percent_close.toFixed(2)}
      />
      <SignalInfoItemVolume
        label="MESSAGE"
        relativeValue={result_system_message}
      />
    </div>
  )
}

export {SignalTradeInfo}
