import s from "./BitcoinPublicInformation.module.css"
import {useContext} from "react";
import {AppDataContext} from "../../context/AppDataContext";
import {parseExportData} from "../../utils/utils";

const BitcoinPublicInformation = () => {
  const value = useContext(AppDataContext);

  if (!value?.bitcoinPublicInformation) {
    return null
  }

  return (
    <div className={s.body}>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>BTC</span>
        <span className={s.itemValue}>{value.bitcoinPublicInformation.current_price}</span>
      </div>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>Moment</span>
        <span className={s.itemValue}>{parseExportData(value.bitcoinPublicInformation.trend_moment)}</span>
      </div>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>Fast</span>
        <span className={s.itemValue}>{parseExportData(value.bitcoinPublicInformation.trend_fast)}</span>
      </div>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>Medium</span>
        <span className={s.itemValue}>{parseExportData(value.bitcoinPublicInformation.trend_medium)}</span>
      </div>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>Slow</span>
        <span className={s.itemValue}>{parseExportData(value.bitcoinPublicInformation.trend_slow)}</span>
      </div>
      <div className={s.itemElement}>
        <span className={s.itemLabel}>HA</span>
        <span className={s.itemValue}>{parseExportData(value.bitcoinPublicInformation.trend_ha)}</span>
      </div>
    </div>
  )
}

export {BitcoinPublicInformation}
