import {rainbowColorEnum} from "../static/static";

const trendData = {
  "-1": "BEAR",
  "0": "FLET",
  "1": "BULL",
}

export const parseExportData = (trendValue: number): string => {
  const trendLabel = trendData[String(trendValue) as keyof typeof trendData]
  return trendLabel ? trendLabel : "NONE"
}

export const getColorDirection = (signal_direction: string): rainbowColorEnum => {
  return signal_direction === "buy"
    ? rainbowColorEnum.green
    : signal_direction === "sell"
      ? rainbowColorEnum.red
      : rainbowColorEnum.gray_1
}


export function hexToRgb(hex: string) {
  // Remove the hash character (#) if present
  hex = hex.replace('#', '');

  // Ensure the hexadecimal value is valid
  const hexRegex = /^[0-9A-Fa-f]{6}$/i;
  if (!hexRegex.test(hex)) {
    throw new Error('Invalid hexadecimal color code');
  }

  // Extract the individual RGB components from the hexadecimal value
  const red = parseInt(hex.substr(0, 2), 16);
  const green = parseInt(hex.substr(2, 2), 16);
  const blue = parseInt(hex.substr(4, 2), 16);

  // Return the RGB color value as a string
  return [red, green, blue];
}

export const getDiffInTimes = (date_start: Date, date_end: Date): [number, number] => {
  // get diff between two dates in minutes and seconds

  const date1 = new Date(date_start);
  const date2 = new Date(date_end);

  const diffInMillis = Math.abs(date2.getTime() - date1.getTime());
  const diffInMinutes = Math.floor(diffInMillis / (1000 * 60));
  const diffInSeconds = Math.floor((diffInMillis / 1000) % 60);

  return [diffInMinutes, diffInSeconds]
}