import React from "react";
import s from "./button.module.css"
import {rainbowColorDict, rainbowColorEnum} from "../../static/static";
import {ReactComponent as LoadingIcon} from "../../assets/LoadingIcon.svg"

type propsType = {
  label: string
  disabled?: boolean
  loaded?: boolean
  color?: rainbowColorEnum
  background?: rainbowColorEnum
  opacity?: number
  className?: string
  width?: number
  clickHandler: () => void
}

const Button: React.FC<propsType> = props => {

  const {
    label,
    disabled,
    loaded,
    color,
    background,
    opacity,
    className,
    width,
    clickHandler,
  } = props

  const onClick = () => {
    if (loaded) {
      return
    }
    if (disabled) {
      return
    }
    clickHandler()
  }

  const activeStyle = {
    "color": rainbowColorDict[color ? color : rainbowColorEnum.dark],
    "background": rainbowColorDict[background ? background : rainbowColorEnum.light],
    "opacity": opacity,
    "width": width,
  }

  const disabledStyle = {
    "color": rainbowColorDict[rainbowColorEnum.gray_1],
    "background": rainbowColorDict[rainbowColorEnum.gray_2],
    "width": width,
  }

  return (
    <div
      className={`
        ${s.button} 
        ${!disabled && !loaded && s.active_button}
        ${className && className}
      `}
      style={disabled ? disabledStyle : activeStyle}
      onClick={onClick}
    >
      {loaded
        ? <LoadingIcon
            fill={rainbowColorDict[color ? color : rainbowColorEnum.dark]}
            height="22px"
          />
        : label
      }
    </div>
  )
}

export {Button}
