import React, {useContext} from "react";
import s from "./NeuronNetworkCurrentHandler.module.css"
import {AppDataContext} from "../../context/AppDataContext";
import {rainbowColorEnum} from "../../static/static";
import { Button } from "../button/Button";

type propsType = {
  neuron_is_current: boolean
}

const NeuronNetworkCurrentHandler: React.FC<propsType> = props => {

  const {
    neuron_is_current
  } = props

  const value = useContext(AppDataContext);

  if (!value) {
    return null
  }
  if (!value.neuronLearningFull) {
    return null
  }

  const changeNeuronNetworkAsCurrent = async () => {
    value.changeNeuronNetworkAsCurrent()
  }

  return (
    <div className={s.flexRow}>
      <Button
        label={neuron_is_current ? "Unset As Current" : "Set As Current"}
        clickHandler={changeNeuronNetworkAsCurrent}
        background={rainbowColorEnum.dark}
        color={neuron_is_current ? rainbowColorEnum.red : rainbowColorEnum.green}
        width={142}
      />
    </div>
  )
}

export {NeuronNetworkCurrentHandler}
