import React, {useContext} from "react";
import s from "./signalDirectionHandler.module.css"
import {AppDataContext} from "../../context/AppDataContext";
import {directionEnum, rainbowColorEnum} from "../../static/static";
import { Button } from "../button/Button";

type propsType = {
  signal_direction: directionEnum
}

const SignalDirectionHandler: React.FC<propsType> = props => {

  const {
    signal_direction
  } = props

  const value = useContext(AppDataContext);

  if (!value) {
    return null
  }
  if (!value.signalFull) {
    return null
  }

  const changeApprovedValue = async (direction: directionEnum) => {
    value.changeDirectionValueForSignalTrade(direction)
  }

  return (
    <div className={s.flexRow}>
      <Button
        label="Mark As BUY"
        clickHandler={() => changeApprovedValue(directionEnum.BUY)}
        background={rainbowColorEnum.dark}
        color={rainbowColorEnum.green}
        disabled={signal_direction === directionEnum.BUY}
        width={120}
      />
      <Button
        label="Mark As FLET"
        clickHandler={() => changeApprovedValue(directionEnum.FLET)}
        background={rainbowColorEnum.dark}
        color={rainbowColorEnum.gray_3}
        disabled={signal_direction === directionEnum.FLET}
        width={120}
      />
      <Button
        label="Mark As SELL"
        clickHandler={() => changeApprovedValue(directionEnum.SELL)}
        background={rainbowColorEnum.dark}
        color={rainbowColorEnum.red}
        disabled={signal_direction === directionEnum.SELL}
        width={120}
      />
    </div>
  )
}

export {SignalDirectionHandler}
