import { useContext, useRef, useEffect } from "react";
import cs from "./styles.module.css";
import { AppDataContext } from "../context/AppDataContext";
import { NeuronLearningPublicItem } from "../components/neuronLearningPublicItem/NeuronLearningPublicItem";

const Neurons = () => {
  const value = useContext(AppDataContext);

  const itemRefs = useRef<any>([]);

  const scrollToItem = () => {
    if (!value) return;
    if (!value.neuronParentUUIDHighlighted) return;
    if (!value.neuronLearningPublicList) return;

    const index = value.neuronLearningPublicList.findIndex(i => i.neuron_uuid === value.neuronParentUUIDHighlighted)
    itemRefs.current[index].scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToItem()
  }, [value?.neuronParentUUIDHighlighted]);

  const neuronLearningPublicItems = value?.neuronLearningPublicList.map((item, index) =>
    <div
      key={item.neuron_uuid}
      ref={(el) => (itemRefs.current[index] = el)}
    >
      <NeuronLearningPublicItem
        neuron={item}
      />
    </div>
  )

  return (
    <div className={cs.list}>
      {neuronLearningPublicItems}
    </div>
  )
}

export {Neurons}
