import React from 'react'
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  registerables,
} from 'chart.js';
import {Chart} from 'react-chartjs-2';
import {
  rainbowColorDict,
  rainbowColorEnum
} from "../../static/static";
import {directionManualSignalType} from "../../types/types";

ChartJS.register(
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  ...registerables
);

type propsType = {
  dataPrice: Array<number>
  dataVolume: Array<number>
  minPrice: number
  maxPrice: number
  directionManualSignal: directionManualSignalType
  createManualSignal: (delta_timestamp: number) => void
}

const Charter: React.FC<propsType> = props => {

  const {
    dataPrice,
    dataVolume,
    minPrice,
    maxPrice,
    directionManualSignal,
    createManualSignal,
  } = props

  const labels: Array<number> = []
  for (let i = dataPrice.length; i > 0; i--) {
    labels.push(i)
  }

  const accentColor = rainbowColorDict[rainbowColorEnum.dark]
  const noAccentColor = rainbowColorDict[rainbowColorEnum.gray_2]

  const options = {
    responsive: true,
    interaction: {
      mode: 'index' as const,
      intersect: false,
    },
    stacked: false,
    onClick: (event: any, elements: any) => {
      if (elements.length > 0) {
        const { datasetIndex, index } = elements[0];
        if (directionManualSignal === "past") {
          createManualSignal(index - dataPrice.length)
        }
        if (directionManualSignal === "future") {
          createManualSignal(index)
        }
      }
    },
    plugins: {
      legend: {
        display: false,
        labels: {
          color: accentColor,
          font: {
            size: 13
          }
        }
      }
    },
    scales: {
      x: {
        grid: {
          display: true,
          drawOnChartArea: false,
          drawTicks: false,
        },
        ticks: {
          color: accentColor,
          maxTicksLimit: 10,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
      y: {
        beginAtZero: false,
        type: 'linear' as const,
        display: true,
        position: 'left' as const,
        min: minPrice,
        max: maxPrice,
        grid: {
          color: noAccentColor,
          tickColor: accentColor,
        },
        ticks: {
          color: accentColor,
          stepSize: (maxPrice-minPrice)/10,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
      y1: {
        type: 'linear' as const,
        display: true,
        position: 'right' as const,
        grid: {
          drawOnChartArea: false,
          tickColor: accentColor,
        },
        ticks: {
          color: accentColor,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
    },
  };

  const data = {
    labels,
    datasets: [
      {
        type: 'line' as const,
        label: 'Price',
        data: dataPrice,
        backgroundColor: 'rgb(130, 30, 30)',
        borderColor: 'rgb(130, 30, 30)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'bar' as const,
        label: 'Volume',
        data: dataVolume,
        backgroundColor: 'rgb(30, 130, 30)',
        borderColor: 'rgb(30, 130, 30)',
        yAxisID: 'y1',
      },
    ],
  };

  return (
    <Chart
      type='bar'
      data={data}
      options={options}
    />
  )
}

export {Charter}