import s from "./NeuronLearningActiveMenu.module.css"
import {ChangeEvent, useContext, useState} from "react";
import {AppDataContext} from "../../context/AppDataContext";
import {
  createNeuronLearningRequestQueryType,
  NeuronLearningRequestQueryType,
  toggleValueType
} from "../../types/types";
import {rainbowColorEnum} from "../../static/static";
import "react-datepicker/dist/react-datepicker.css";
import {Button} from "../button/Button";
import {Pagination} from "../pagination/Pagination"
import {ComplexToggle} from "../complexToggle/ComplexToggle";

const NeuronLearningActiveMenu = () => {

  const value = useContext(AppDataContext);

  const [selectedCreateLearningEpoch, setSelectedCreateLearningEpoch] = useState<number>(value?.createNeuronLearningRequestQuery.epochs || 1000);
  const [selectedCreateLearningRate, setSelectedCreateLearningRate] = useState<number>(value?.createNeuronLearningRequestQuery.learning_rate || 0.1);
  const [selectedCreateLearningLimit, setSelectedCreateLearningLimit] = useState<number>(value?.createNeuronLearningRequestQuery.limit || 1000);
  const [selectedCreateLearningOffset, setSelectedCreateLearningOffset] = useState<number>(value?.createNeuronLearningRequestQuery.offset || 0);

  const [selectedNetworkTagName, setSelectedNetworkTagName] = useState<string>("");
  const [selectedNetworkIsCurrent, setSelectedNetworkIsCurrent] = useState<toggleValueType>(0);
  const [selectedNetworkLimit, setSelectedNetworkLimit] = useState<number>(100);
  const [selectedNetworkPage, setSelectedNetworkPage] = useState<number>(1);
  const [selectedNetworkDesc, setSelectedNetworkDesc] = useState<toggleValueType>(-1)

  const selectCreateLearningEpoch = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedCreateLearningEpoch(+e.target.value)
  }
  const selectCreateLearningRate = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedCreateLearningRate(+e.target.value)
  }
  const selectCreateLearningLimit = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedCreateLearningLimit(+e.target.value)
  }
  const selectCreateLearningOffset = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedCreateLearningOffset(+e.target.value)
  }

  const selectTagName = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedNetworkTagName(e.target.value)
  }
  const selectNetworkLimit = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedNetworkLimit(+e.target.value)
  }

  const submitCreateNeuronLearning = () => {
    // function for submit payload and set new task for creating neuron network learning
    if (!value) {
      return null
    }
    const createNeuronLearningRequestQuery: createNeuronLearningRequestQueryType = {
      epochs: selectedCreateLearningEpoch,
      learning_rate: selectedCreateLearningRate,
      limit: selectedCreateLearningLimit,
      offset: selectedCreateLearningOffset,
    }
    value.setCreateNeuronLearningRequestQuery(createNeuronLearningRequestQuery)
  }

  const submitCheckNeuronLearning = () => {
    // function for submit payload and set new task for creating neuron network learning
    if (!value) {
      return null
    }
    const createNeuronLearningRequestQuery: createNeuronLearningRequestQueryType = {
      epochs: selectedCreateLearningEpoch,
      learning_rate: selectedCreateLearningRate,
      limit: selectedCreateLearningLimit,
      offset: selectedCreateLearningOffset,
    }
    value.checkNeuronLearningRequest(createNeuronLearningRequestQuery)
  }

  const submitSearchNeuronLearningList = () => {
    // function for submit payload and search list of neuron learning network
    if (!value) {
      return null
    }

    const neuronLearningRequestQuery: NeuronLearningRequestQueryType = {
      neuron_tag_name: selectedNetworkTagName.length > 0
        ? selectedNetworkTagName
        : undefined,
      neuron_is_current: selectedNetworkIsCurrent,

      offset: selectedNetworkPage*selectedNetworkLimit - selectedNetworkLimit,
      limit: selectedNetworkLimit,

      is_desc: selectedNetworkDesc === 1,
    }
    value.setNeuronLearningRequestQuery(neuronLearningRequestQuery)
  }

  if (!value) return null

  return (
    <div className={s.wrapper}>
      <div className={s.body}>
        <div className={s.menu_start}>

          <div className={s.menuInput}>
            <span>Learning Epoch: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedCreateLearningEpoch}
              onChange={(e) => selectCreateLearningEpoch(e)}
            />
          </div>

          <div className={s.menuInput}>
            <span>Learning Rate: </span>
            <input
              step={0.05}
              style={{width: "60px"}}
              type="number"
              value={selectedCreateLearningRate}
              onChange={(e) => selectCreateLearningRate(e)}
            />
          </div>

          <div className={s.menuInput}>
            <span>Learning Limit: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedCreateLearningLimit}
              onChange={(e) => selectCreateLearningLimit(e)}
            />
          </div>

          <div className={s.menuInput}>
            <span>Learning Offset: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedCreateLearningOffset}
              onChange={(e) => selectCreateLearningOffset(e)}
            />
          </div>

          <div className={s.newline}></div>

          <div className={s.menuInput}>
            <span>Tag Name: </span>
            <input
              style={{width: "90px"}}
              type="text"
              value={selectedNetworkTagName}
              onChange={(e) => selectTagName(e)}
            />
          </div>

          <ComplexToggle
            label="Is Current"
            value={selectedNetworkIsCurrent}
            onChange={setSelectedNetworkIsCurrent}
            isComplex
          />

          <div className={s.menuInput}>
            <span>Search Limit: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedNetworkLimit}
              onChange={(e) => selectNetworkLimit(e)}
            />
          </div>

          <Pagination selectedPage={selectedNetworkPage} selectPage={setSelectedNetworkPage} />

          <ComplexToggle
            label="Desc/Asc"
            value={selectedNetworkDesc}
            onChange={setSelectedNetworkDesc}
          />

          {value.checkNeuronLearning && <>
            <div>Train Buy: {value.checkNeuronLearning.neuron_item_count_train_buy}</div>
            <div>Train Sell: {value.checkNeuronLearning.neuron_item_count_train_sell}</div>
            <div>Train Flet: {value.checkNeuronLearning.neuron_item_count_train_flet}</div>
            <div>Real Buy: {value.checkNeuronLearning.neuron_item_count_real_buy}</div>
            <div>Real Sell: {value.checkNeuronLearning.neuron_item_count_real_sell}</div>
            <div>Real Flet: {value.checkNeuronLearning.neuron_item_count_real_flet}</div>
          </>}
        </div>

        <div className={s.menu_end}>
          <Button
            label="Check Neuron Learning"
            loaded={value.loadingCreateNeuronLearning}
            color={rainbowColorEnum.blue}
            background={rainbowColorEnum.dark}
            clickHandler={submitCheckNeuronLearning}
          />
          <Button
            label="New Neuron Learning"
            loaded={value.loadingCreateNeuronLearning}
            color={rainbowColorEnum.green}
            background={rainbowColorEnum.dark}
            clickHandler={submitCreateNeuronLearning}
          />
          <Button
            label="Search"
            loaded={value.loadingNeuronLearningPublicList}
            color={rainbowColorEnum.green}
            background={rainbowColorEnum.dark}
            clickHandler={submitSearchNeuronLearningList}
          />
        </div>
      </div>
    </div>
  )
}

export {NeuronLearningActiveMenu}
