import React from "react";
import s from "./NeuronLearningDurationTime.module.css"
import {getDiffInTimes} from "../../utils/utils";

type propsType = {
  created_at: Date
  finish_at?: Date
}

const NeuronLearningDurationTime: React.FC<propsType> = props => {

  const {
    created_at,
    finish_at,
  } = props

  if (!finish_at) {
    return (
      <div className={s.infoMain}>
        <div></div>
        <div className={s.infoPrefix}>
          IN PROGRESS
        </div>
      </div>
    )
  }

  const [diffInMinutes, diffInSeconds] = getDiffInTimes(created_at, finish_at)

  return (
    <div className={s.infoMain}>
      <div className={s.infoPrefix}>
        DURATION:
      </div>
      <div className={s.infoGroup}>
        <div className={s.infoTitle}>
          {diffInMinutes}
        </div>
        <div className={s.infoPercent}>{`M `}</div>
        <div className={s.infoTitle}>
          {diffInSeconds}
        </div>
        <div className={s.infoPercent}>S</div>
      </div>
    </div>
  )
}

export {NeuronLearningDurationTime}
