import React from "react";
import s from "./pagination.module.css"
import {Button} from "../button/Button";

type propsType = {
  selectedPage: number
  selectPage: (value: number) => void
}

const Pagination: React.FC<propsType> = props => {

  const {
    selectedPage,
    selectPage
  } = props

  const onClickHandler = (value: number) => {
    if (value === 0) {
      return null
    }
    selectPage(value)
  }

  return (
    <div
      className={s.pagination}
    >
      <Button
        label={"<<"}
        className={s.button}
        clickHandler={() => onClickHandler(selectedPage - 1)}
        disabled={selectedPage===1}
      />
      <Button
        label={String(selectedPage)}
        className={s.button}
        clickHandler={() => onClickHandler(1)}
      />
      <Button
        label={">>"}
        className={s.button}
        clickHandler={() => onClickHandler(selectedPage + 1)}
      />
    </div>
  )
}

export {Pagination}
