import React, {useContext, useState} from "react";
import s from "./activeSymbolTier.module.css"
import {rainbowColorEnum, symbolTierList} from "../../static/static";
import {AppDataContext} from "../../context/AppDataContext";
import {adminApi} from "../../api/api";
import {UpdateActiveSymbolTierPayloadType} from "../../types/types";
import { Button } from "../button/Button";

type propsType = {
  symbol_tier: number
  symbol_uuid: string
}

const ActiveSymbolTier: React.FC<propsType> = props => {

  const {
    symbol_tier,
    symbol_uuid
  } = props

  const value = useContext(AppDataContext);

  const [currentTier, setCurrentTier] = useState<number>(symbol_tier)
  const [loaded, setLoaded] = useState<boolean>(false)

  const submitTier = async () => {
    if (!value) {
      return null
    }
    setLoaded(true)
    const payload: UpdateActiveSymbolTierPayloadType = {
      symbol_tier: currentTier,
      symbol_uuid
    }
    try {
      await adminApi.updateActiveSymbolTier(payload)
      await value.getActiveSymbolList()
    } catch (e: any) {
      console.log(e.message)
    }
    setLoaded(false)
  }

  const tiers = symbolTierList.map(item => {
    return (
      <Button
        key={item.tier}
        clickHandler={() => setCurrentTier(item.tier)}
        color={item.color}
        background={item.background}
        opacity={currentTier === item.tier ? 0.9 : 0.3}
        label={item.label}
      />
    )
  })

  return (
    <div className={s.wrapper}>
      <div className={s.tierGroup}>
        {tiers}
      </div>
      <div className={s.tierGroup}>
        <Button
          label="Submit"
          loaded={loaded}
          disabled={currentTier === symbol_tier}
          color={rainbowColorEnum.green}
          background={rainbowColorEnum.dark}
          clickHandler={submitTier}
        />
        <Button
          label="Decline"
          loaded={loaded}
          disabled={currentTier === symbol_tier}
          color={rainbowColorEnum.red}
          background={rainbowColorEnum.dark}
          clickHandler={() => setCurrentTier(symbol_tier)}
        />
      </div>
    </div>
  )
}

export {ActiveSymbolTier}