import React from "react";
import { directionEnum, rainbowColorEnum, SignalStatusReportEnum } from "../../static/static";
import { Button } from "../button/Button";
import { adminApi } from "../../api/api";
import { CustomChip } from "../customChip/CustomChip";

type propsType = {
  signal_uuid: string
  signal_status_report: SignalStatusReportEnum
  signal_is_approved: boolean
}

const SignalReportHandler: React.FC<propsType> = props => {
  const {
    signal_uuid,
    signal_status_report,
    signal_is_approved,
  } = props

  const changeApprovedValue = async () => {
    try {
      await adminApi.changeApproveValueForSignalTradeByUUID(signal_uuid, true)
    } catch (e: any) {
      console.log(e.message)
    }
  }

  const changeToFletAndApprove = async () => {
    try {
      await adminApi.changeDirectionValueForSignalTradeByUUID(signal_uuid, directionEnum.FLET)
      await adminApi.changeApproveValueForSignalTradeByUUID(signal_uuid, true)
    } catch (e: any) {
      console.log(e.message)
    }
  }

  if (signal_is_approved) {
    return (
      <CustomChip
        label="Approved"
        background={rainbowColorEnum.violet}
        color={rainbowColorEnum.light}
      />
    )
  }

  if (signal_status_report === SignalStatusReportEnum.DEFAULT) {
    return (
      <Button
        label="Set Approved"
        clickHandler={changeApprovedValue}
        background={rainbowColorEnum.dark}
        color={rainbowColorEnum.green}
        width={120}
      />
    )
  }

  return (
    <Button
      label="To Flet and Approve"
      clickHandler={changeToFletAndApprove}
      background={rainbowColorEnum.white}
      color={rainbowColorEnum.dark}
      width={160}
    />
  )
}

export {SignalReportHandler}
