import React from "react";
import s from "./priceItemNew.module.css"

type propsType = {
  label: string
  price_enter: number | string
  is_percent?: boolean
}

const PriceItemNew: React.FC<propsType> = props => {

  const {
    label,
    price_enter,
    is_percent = false,
  } = props

  return (
    <div className={s.infoMain}>
      <div className={s.infoPrefix}>
        {label}
      </div>
      <div className={s.infoGroup}>
        <div className={s.infoTitle}>
          {price_enter}
        </div>
        {is_percent && <div
            className={s.infoPercent}
        >
          %
        </div>}
      </div>
    </div>
  )
}

export {PriceItemNew}
