import React, {useContext} from "react";
import s from "./signalExcludedHandler.module.css"
import {AppDataContext} from "../../context/AppDataContext";
import {rainbowColorEnum} from "../../static/static";
import { Button } from "../button/Button";

type propsType = {
  signal_is_excluded: boolean
}

const SignalExcludedHandler: React.FC<propsType> = props => {

  const {
    signal_is_excluded
  } = props

  const value = useContext(AppDataContext);

  if (!value) {
    return null
  }
  if (!value.signalFull) {
    return null
  }

  const changeExcludedValue = async () => {
    value.changeExcludedValueForSignalTrade()
  }

  return (
    <div className={s.flexRow}>
      <Button
        label={signal_is_excluded ? "Set As Included" : "Set As Excluded"}
        clickHandler={changeExcludedValue}
        background={rainbowColorEnum.dark}
        color={signal_is_excluded ? rainbowColorEnum.green : rainbowColorEnum.red}
        width={142}
      />
    </div>
  )
}

export {SignalExcludedHandler}
