import React from "react";
import s from "./SignalDirectionDisplayComplex.module.css"
import {directionBackgroundDict, directionColorDict, directionEnum, rainbowColorEnum} from "../../static/static";
import {CustomChip} from "../customChip/CustomChip";

type propsType = {
  signal_direction_created: directionEnum
  signal_direction_updated?: directionEnum
}

const SignalDirectionDisplayComplex: React.FC<propsType> = props => {

  const {
    signal_direction_created,
    signal_direction_updated
  } = props

  const direction_first = signal_direction_updated ? signal_direction_updated : signal_direction_created

  return (
    <div className={s.directionMain}>
      <div className={s.item}>
        <CustomChip
          label={direction_first.toUpperCase()}
          background={directionBackgroundDict[direction_first] as rainbowColorEnum}
          color={directionColorDict[direction_first] as rainbowColorEnum}
        />
      </div>
      {signal_direction_updated && <div className={s.directionSecondary}>
        {signal_direction_created.toUpperCase()}
      </div>}
    </div>
  )
}

export {SignalDirectionDisplayComplex}
