import React, {useContext} from "react";
import s from "./SymbolItem.module.css"
import {SymbolPublicWithPlatformPublicType} from "../../types/types";
import {ActiveSymbolTier} from "../activeSymbolTier/ActiveSymbolTier";
import {CustomChip} from "../customChip/CustomChip";
import {InfoItemMain} from "../infoItemMain/InfoItemMain";
import {AppDataContext} from "../../context/AppDataContext";

type propsType = {
  symbol: SymbolPublicWithPlatformPublicType
}

const SymbolItem: React.FC<propsType> = props => {

  const {
    symbol
  } = props

  const value = useContext(AppDataContext);
  if (!value) return null

  return (
    <div className={s.wrapper}>
      <div className={s.info}>
        <InfoItemMain
          title={symbol.symbol_name}
          description={symbol.symbol_pair}
        />

        <CustomChip
          label={symbol.platform.platform_name}
          background={symbol.platform.platform_background}
          color={symbol.platform.platform_color}
        />
      </div>
      <div>
        <ActiveSymbolTier symbol_tier={symbol.symbol_tier} symbol_uuid={symbol.symbol_uuid}/>
      </div>
    </div>
  )
}

export {SymbolItem}