import React from "react";
import s from "./ComplexToggle.module.css"
import {toggleValueType} from "../../types/types";

type propsType = {
  label: string
  value: toggleValueType
  onChange: (value: toggleValueType) => void
  isComplex?: boolean
}

const ComplexToggle: React.FC<propsType> = props => {

  const {
    label,
    value,
    onChange,
    isComplex,
  } = props

  const position = ["left", "center", "right"]

  return (
    <div className={s.toggle}>
      <span>{label}</span>
      <div className={s.mainContainer}>
        <div className={`${s.switcher} ${s[position[value+1]]}`} />
        <div
          className={s.item}
          onClick={() => onChange(-1)}
        />
        {isComplex && <div
          className={s.item}
          onClick={() => onChange(0)}
        />}
        <div
          className={s.item}
          onClick={() => onChange(1)}
        />
      </div>
    </div>
  )
}

export {ComplexToggle}
