import React from "react";
import s from "./NeuronLearningRealInfo.module.css"
import {SignalInfoItemVolume} from "../signalInfoItemVolume/SignalInfoItemVolume";
import {NeuronLearningDurationTime} from "../neuronLearningDurationTime/NeuronLearningDurationTime";

type propsType = {
  created_at: Date
  finish_at?: Date

  neuron_item_count_real_buy?: number
  neuron_item_count_real_sell?: number
  neuron_item_count_real_flet?: number
  neuron_item_count_real_total?: number

  neuron_loss_real_last?: number
  neuron_correct_real_last?: number
}

const NeuronLearningRealInfo: React.FC<propsType> = props => {

  const {
    created_at,
    finish_at,

    neuron_item_count_real_buy,
    neuron_item_count_real_sell,
    neuron_item_count_real_flet,
    neuron_item_count_real_total,

    neuron_loss_real_last,
    neuron_correct_real_last,
  } = props

  return (
    <div className={s.container}>

      <div>
        <NeuronLearningDurationTime
          created_at={created_at}
          finish_at={finish_at}
        />
      </div>

      <div></div>

      <SignalInfoItemVolume
        label="COUNT BUY"
        relativeValue={neuron_item_count_real_buy}
      />
      <SignalInfoItemVolume
        label="COUNT SELL"
        relativeValue={neuron_item_count_real_sell}
      />
      <SignalInfoItemVolume
        label="COUNT FLET"
        relativeValue={neuron_item_count_real_flet}
      />
      <SignalInfoItemVolume
        label="COUNT TOTAL"
        relativeValue={neuron_item_count_real_total}
      />

      <div></div>
      <div></div>

      <SignalInfoItemVolume
        label="REAL LOSS:"
        value="%"
        relativeValue={Number(neuron_loss_real_last).toFixed(2)}
      />
      <SignalInfoItemVolume
        label="REAL CORRECT:"
        relativeValue={Number(neuron_correct_real_last)}
      />

      <div></div>
      <div></div>
    </div>
  )
}

export {NeuronLearningRealInfo}
