import React, {useContext} from "react";
import s from "./NeuronNetworkFullItemModal.module.css"
import ReactDOM from 'react-dom'
import {AppDataContext} from "../../context/AppDataContext";
import {Button} from "../button/Button";
import {
  rainbowColorEnum
} from "../../static/static";
import {CustomChip} from "../customChip/CustomChip";
import {ChartNeuronLearning} from "../chartNeuronLerning/ChartNeuronLerning";
import {NeuronLearningTrainInfo} from "../neuronLearningTrainInfo/NeuronLearningTrainInfo";
import {NeuronLearningRealInfo} from "../neuronLearningRealInfo/NeuronLearningRealInfo";
import {NeuronNetworkCurrentHandler} from "../neuronNetworkCurrentHandler/NeuronNetworkCurrentHandler";
import {NeuronTagEditor} from "../neuronTagEditor/NeuronTagEditor";
import {createNeuronLearningRequestQueryType} from "../../types/types";

const NeuronNetworkFullItemModal: React.FC = () => {

  const value = useContext(AppDataContext);
  if (!value) {
    return null
  }
  if (!value.neuronLearningFull) {
    return null
  }

  const neuronLearningFull = value.neuronLearningFull

  const closeModal = () => {
    value.closeNeuronLearningFullModal()
  }

  const removeHandler = () => {
    value.removeLearningNeuronNetwork(neuronLearningFull.neuron_uuid)
  }

  const detailHandler = (neuron_uuid?: string) => {
    if (!neuron_uuid) {
      return
    }
    value.getNeuronLearningFullByUUID(neuron_uuid)
  }

  const submitCreateNeuronLearning = (neuron_uuid?: string) => {
    // function for submit payload and set new task for creating neuron network learning
    if (!value) {
      return null
    }
    if (!neuron_uuid) {
      return
    }
    const createNeuronLearningRequestQuery: createNeuronLearningRequestQueryType = {
      ...value.createNeuronLearningRequestQuery,
      neuron_parent_uuid: neuron_uuid
    }
    value.setCreateNeuronLearningRequestQuery(createNeuronLearningRequestQuery)
  }

  return ReactDOM.createPortal(
    <>
      <div className={s.modalBackground} onClick={closeModal}/>
      <div className={s.modalContainer}>
        <div className={s.modalHeader}>
          <NeuronTagEditor
            neuron_uuid={neuronLearningFull.neuron_uuid}
            neuron_tag_name={neuronLearningFull.neuron_tag_name}
          />

          <div className={s.flexRow}>
            <div>
              {new Date(neuronLearningFull.created_at).toLocaleString()}
            </div>

            <Button
              label="Remove"
              clickHandler={removeHandler}
              background={rainbowColorEnum.dark}
              color={rainbowColorEnum.dark_red}
            />

            <Button
              label="Close"
              clickHandler={closeModal}
              background={rainbowColorEnum.dark}
              color={rainbowColorEnum.red}
            />
          </div>
        </div>
        <div className={s.modalHeader}>
          <div className={s.flexRow}>

            <NeuronNetworkCurrentHandler
              neuron_is_current={neuronLearningFull.neuron_is_current}
            />

            {neuronLearningFull.neuron_is_current && <CustomChip
              label="Current"
              background={rainbowColorEnum.dark_green}
              color={rainbowColorEnum.light}
            />}

          </div>

          <div className={s.flexRow}>

            <Button
              clickHandler={() => submitCreateNeuronLearning(neuronLearningFull.neuron_uuid)}
              label="Create New"
            />

            <div>
              {neuronLearningFull.neuron_uuid}
            </div>

            <div>
              {neuronLearningFull.neuron_parent_uuid && <Button
                  clickHandler={() => detailHandler(neuronLearningFull.neuron_parent_uuid)}
                  label={neuronLearningFull.neuron_parent_uuid}
              />}
            </div>

          </div>
        </div>

        <div className={s.modalBody}>
          <div className={s.tableBLock}>
            <div>
              <ChartNeuronLearning
                lossTotalDataset={neuronLearningFull.neuron_loss_total_train_dataset}
                correctTotalDataset={neuronLearningFull.neuron_correct_total_train_dataset}

                lossBuyDataset={neuronLearningFull.neuron_loss_buy_train_dataset}
                correctBuyDataset={neuronLearningFull.neuron_correct_buy_train_dataset}

                lossSellDataset={neuronLearningFull.neuron_loss_sell_train_dataset}
                correctSellDataset={neuronLearningFull.neuron_correct_sell_train_dataset}

                lossFletDataset={neuronLearningFull.neuron_loss_flet_train_dataset}
                correctFletDataset={neuronLearningFull.neuron_correct_flet_train_dataset}
              />
              <NeuronLearningTrainInfo
                neuron_epochs={neuronLearningFull.neuron_epochs}
                neuron_learning_rate={neuronLearningFull.neuron_learning_rate}
                neuron_limit={neuronLearningFull.neuron_limit}
                neuron_offset={neuronLearningFull.neuron_offset}

                neuron_item_count_train_buy={neuronLearningFull.neuron_item_count_train_buy}
                neuron_item_count_train_sell={neuronLearningFull.neuron_item_count_train_sell}
                neuron_item_count_train_flet={neuronLearningFull.neuron_item_count_train_flet}
                neuron_item_count_train_total={neuronLearningFull.neuron_item_count_train_total}

                neuron_loss_train_last={neuronLearningFull.neuron_loss_train_last}
                neuron_correct_train_last={neuronLearningFull.neuron_correct_train_last}
              />
            </div>
            <div>
              <ChartNeuronLearning
                lossTotalDataset={neuronLearningFull.neuron_loss_total_real_dataset}
                correctTotalDataset={neuronLearningFull.neuron_correct_total_real_dataset}

                lossBuyDataset={neuronLearningFull.neuron_loss_buy_real_dataset}
                correctBuyDataset={neuronLearningFull.neuron_correct_buy_real_dataset}

                lossSellDataset={neuronLearningFull.neuron_loss_sell_real_dataset}
                correctSellDataset={neuronLearningFull.neuron_correct_sell_real_dataset}

                lossFletDataset={neuronLearningFull.neuron_loss_flet_real_dataset}
                correctFletDataset={neuronLearningFull.neuron_correct_flet_real_dataset}
              />
              <NeuronLearningRealInfo
                created_at={neuronLearningFull.created_at}
                finish_at={neuronLearningFull.finish_at}

                neuron_item_count_real_buy={neuronLearningFull.neuron_item_count_real_buy}
                neuron_item_count_real_sell={neuronLearningFull.neuron_item_count_real_sell}
                neuron_item_count_real_flet={neuronLearningFull.neuron_item_count_real_flet}
                neuron_item_count_real_total={neuronLearningFull.neuron_item_count_real_total}

                neuron_loss_real_last={neuronLearningFull.neuron_loss_real_last}
                neuron_correct_real_last={neuronLearningFull.neuron_correct_real_last}
              />
            </div>
          </div>
        </div>
      </div>
    </>,
    document.body,
  );
}

export {NeuronNetworkFullItemModal}
