import React from "react";
import s from "./infoItemMain.module.css"

type propsType = {
  title: string
  description?: string
  titleCallBack?: (value: string) => void
}

const InfoItemMain: React.FC<propsType> = props => {

  const {
    title,
    description,
    titleCallBack,
  } = props

  return (
    <div className={s.infoMain}>
      <div
        className={`${s.infoTitle} ${titleCallBack && s.titleActive}`}
        onClick={() => {
          titleCallBack && titleCallBack(title)
        }}
      >
        {title}
      </div>
      {description && <div className={s.infoDescription}>
        {description.toUpperCase()}
      </div>}
    </div>
  )
}

export {InfoItemMain}
