import s from "./signalsTraderActiveMenu.module.css"
import {ChangeEvent, useContext, useEffect, useState} from "react";
import {AppDataContext} from "../../context/AppDataContext";
import Select from 'react-select';
import {
  multiSelectType,
  SignalTraderRequestQueryType,
  toggleValueType
} from "../../types/types";
import {availableSignalDirectionList, rainbowColorEnum} from "../../static/static";
import "react-datepicker/dist/react-datepicker.css";
import {CustomDatePicker} from "../customDatePicker/CustomDatePicker";
import {Button} from "../button/Button";
import {Pagination} from "../pagination/Pagination"
import {ComplexToggle} from "../complexToggle/ComplexToggle";
import {SignalReportBulkHandler} from "../signalReportBulkHandler/SignalReportBulkHandler";

const SignalsTraderActiveMenu = () => {

  const value = useContext(AppDataContext);

  const [visible, setVisible] = useState<boolean>(true)

  const [availablePlatformList, setAvailablePlatformList] = useState<multiSelectType[]>([]);
  const [availableSignalSymbolList, setAvailableSignalSymbolList] = useState<multiSelectType[]>([]);

  const [selectedPlatformList, setSelectedPlatformList] = useState<multiSelectType[]>([]);
  const [selectedSignalSymbolList, setSelectedSignalSymbolList] = useState<multiSelectType[]>([]);
  const [selectedSignalDirectionList, setSelectedSignalDirectionList] = useState<multiSelectType[]>([])

  const [selectedSignalIsApproved, setSelectedSignalIsApproved] = useState<toggleValueType>(0)
  const [selectedSignalIsExcluded, setSelectedSignalIsExcluded] = useState<toggleValueType>(0)

  const [selectedSignalIsManual, setSelectedSignalIsManual] = useState<toggleValueType>(0)
  const [selectedSignalIsNeuronNetwork, setSelectedSignalIsNeuronNetwork] = useState<toggleValueType>(0)

  const [selectedSignalForLearn, setSelectedSignalForLearn] = useState<toggleValueType>(0)

  const [selectedStartDate, setSelectedStartDate] = useState<Date | null>(null);
  const [selectedEndDate, setSelectedEndDate] = useState<Date | null>(null);
  const [selectedLimit, setSelectedLimit] = useState<number>(10);
  const [selectedPage, setSelectedPage] = useState<number>(1);

  const [selectedSignalsComplex, setSelectedSignalsComplex] = useState<toggleValueType>(-1)
  const [selectedSignalsDesc, setSelectedSignalsDesc] = useState<toggleValueType>(-1)

  const [selectedRoeValue, setSelectedRoeValue] = useState<number>(0)
  const [selectedRoeCompare, setSelectedRoeCompare] = useState<toggleValueType>(0)

  const selectPlatformList = (e: any) => {
    setSelectedPlatformList(e)
  }
  const selectSignalSymbolList = (e: any) => {
    setSelectedSignalSymbolList(e)
  }
  const selectSignalDirectionList = (e: any) => {
    setSelectedSignalDirectionList(e)
  }
  const selectStartDate = (e: Date | null) => {
    setSelectedStartDate(e)
  }
  const selectEndDate = (e: Date | null) => {
    // TODO create helper func
    e?.setHours(23)
    e?.setMinutes(59)
    e?.setSeconds(59)
    e?.setMilliseconds(99)
    setSelectedEndDate(e)
  }
  const selectLimit = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedLimit(+e.target.value)
  }
  const selectRoeValue = (e: ChangeEvent<HTMLInputElement>) => {
    setSelectedRoeValue(+e.target.value)
  }

  const submitHandler = () => {
    // TODO description
    if (!value) {
      return null
    }
    // TODO create helper func
    const signalTraderRequestQuery: SignalTraderRequestQueryType = {
      signal_symbol_pair: selectedSignalSymbolList.length > 0
        ? selectedSignalSymbolList.map(item => item.value)
        : undefined,
      signal_direction: selectedSignalDirectionList.length > 0
        ? selectedSignalDirectionList.map(item => item.value)
        : undefined,

      signal_direction_complex: selectedSignalsComplex === 1,

      signal_is_approved: selectedSignalIsApproved,
      signal_is_excluded: selectedSignalIsExcluded,

      signal_is_manual: selectedSignalIsManual,
      signal_is_neuron_network: selectedSignalIsNeuronNetwork,

      signal_for_learn: selectedSignalForLearn,

      platform_name: selectedPlatformList.length > 0
        ? selectedPlatformList.map(item => item.value)
        : undefined,

      start_date: selectedStartDate ? selectedStartDate : undefined,
      end_date: selectedEndDate ? selectedEndDate : undefined,

      offset: selectedPage*selectedLimit - selectedLimit,
      limit: selectedLimit,

      is_desc: selectedSignalsDesc === 1,

      trade_roe_value: selectedRoeValue,
      trade_roe_compare: selectedRoeCompare === 1 ? "more" : (selectedRoeCompare === -1 ? "less" : undefined)
    }
    value.setSignalTraderRequestQuery(signalTraderRequestQuery)
  }

  const fillMenuItemList = () => {
    if (!value) {
      return null
    }

    const platformList: multiSelectType[] = []
    Object.keys(value.platformDict).forEach(item => platformList.push({
      value: item,
      label: item
    }))
    setAvailablePlatformList(platformList)

    const signalSymbolList: multiSelectType[] = []
    value?.symbolList.forEach(item => signalSymbolList.push({
      value: item.symbol_pair,
      label: item.symbol_pair
    }))
    setAvailableSignalSymbolList(signalSymbolList)
  }

  useEffect(() => {
    fillMenuItemList()
  }, [value?.symbolList, value?.platformDict])

  useEffect(() => {
    if (value?.signalActiveSearchElement) {
      const item: multiSelectType = {
        value: value.signalActiveSearchElement,
        label: value.signalActiveSearchElement,
      }
      setSelectedSignalSymbolList([item])
    }
    setVisible(false)
  }, [value?.signalActiveSearchElement])

  useEffect(() => {
    if (!visible) {
      setVisible(true)
    }
  }, [visible])

  if (!value) return null
  if (!visible) return null

  return (
    <div className={s.wrapper}>
      <div className={s.body}>
        <div className={s.menu_start}>
          <Select
            isMulti
            defaultValue={selectedPlatformList}
            onChange={(e) => selectPlatformList(e)}
            options={availablePlatformList}
            placeholder="Platform"
          />
          <Select
            isMulti
            defaultValue={selectedSignalSymbolList}
            onChange={(e) => selectSignalSymbolList(e)}
            options={availableSignalSymbolList}
            placeholder="Symbol"
          />
          <Select
            isMulti
            defaultValue={selectedSignalDirectionList}
            onChange={(e) => selectSignalDirectionList(e)}
            options={availableSignalDirectionList}
            placeholder="Direction"
          />

          <ComplexToggle
            label="Approved"
            value={selectedSignalIsApproved}
            onChange={setSelectedSignalIsApproved}
            isComplex
          />
          <ComplexToggle
            label="Excluded"
            value={selectedSignalIsExcluded}
            onChange={setSelectedSignalIsExcluded}
            isComplex
          />

          <ComplexToggle
            label="Manual"
            value={selectedSignalIsManual}
            onChange={setSelectedSignalIsManual}
            isComplex
          />
          <ComplexToggle
            label="NN"
            value={selectedSignalIsNeuronNetwork}
            onChange={setSelectedSignalIsNeuronNetwork}
            isComplex
          />

          <ComplexToggle
            label="For Learn"
            value={selectedSignalForLearn}
            onChange={setSelectedSignalForLearn}
            isComplex
          />

          <ComplexToggle
            label="Complex Direction"
            value={selectedSignalsComplex}
            onChange={setSelectedSignalsComplex}
          />
          <ComplexToggle
            label="Desc/Asc"
            value={selectedSignalsDesc}
            onChange={setSelectedSignalsDesc}
          />

          <CustomDatePicker
            label="Start: "
            selected={selectedStartDate}
            onChange={(date) => selectStartDate(date)}
          />
          <CustomDatePicker
            label="End: "
            selected={selectedEndDate}
            onChange={(date) => selectEndDate(date)}
          />

          <ComplexToggle
            label="Compare"
            value={selectedRoeCompare}
            onChange={setSelectedRoeCompare}
            isComplex
          />

          <div className={s.menuInput}>
            <span>Roe Value: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedRoeValue}
              onChange={(e) => selectRoeValue(e)}
            />
          </div>

          <div className={s.menuInput}>
            <span>Limit: </span>
            <input
              style={{width: "60px"}}
              type="number"
              value={selectedLimit}
              onChange={(e) => selectLimit(e)}
            />
          </div>

          <Pagination selectedPage={selectedPage} selectPage={setSelectedPage}/>

          <SignalReportBulkHandler
            signalUuidToApproveList={value.signalUuidToApproveList}
            signalUuidToFletList={value.signalUuidToFletList}
          />

        </div>
        <div className={s.menu_end}>
          <Button
            label="Search"
            loaded={value.loadingSignalPublicList}
            color={rainbowColorEnum.green}
            background={rainbowColorEnum.dark}
            clickHandler={submitHandler}
          />
        </div>
      </div>
    </div>
  )
}

export {SignalsTraderActiveMenu}
