import React from "react";
import s from "./signalInfoItemVolume.module.css"

type propsType = {
  label: string
  value?: number | string
  relativeValue?: number | string
}

const SignalInfoItemVolume: React.FC<propsType> = props => {

  const {
    label,
    value,
    relativeValue,
  } = props

  return (
    <div>
      <div className={s.infoMain}>
        <div className={s.infoPrefix}>
          {label}
        </div>
        <div className={s.infoGroup}>
          <div className={s.infoTitle}>
            {relativeValue}
          </div>
          {value && <div className={s.infoPercent}>{value}</div>}
        </div>
      </div>
    </div>
  )
}

export {SignalInfoItemVolume}
