import React from "react";
import s from "./customChip.module.css"
import {rainbowColorDict, rainbowColorEnum} from "../../static/static";

type propsType = {
  label: string
  background: rainbowColorEnum
  color: rainbowColorEnum
  width?: number
}

const CustomChip: React.FC<propsType> = props => {

  const {
    label,
    background,
    color,
    width,
  } = props

  return (
    <div
      className={s.chip}
      style={{
        "background": rainbowColorDict[background],
        "color": rainbowColorDict[color],
        // "width": `${width ? width : 72}px`,
      }}
    >
      {label}
    </div>
  )
}

export {CustomChip}
