import React from 'react'
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  registerables,
} from 'chart.js';
import {Chart} from 'react-chartjs-2';
import {
  rainbowColorDict,
  rainbowColorEnum
} from "../../static/static";

ChartJS.register(
  LinearScale,
  CategoryScale,
  BarElement,
  PointElement,
  LineElement,
  Legend,
  Tooltip,
  ...registerables
);

type propsType = {
  lossTotalDataset?: Array<number>
  correctTotalDataset?: Array<number>

  lossBuyDataset?: Array<number>
  correctBuyDataset?: Array<number>

  lossSellDataset?: Array<number>
  correctSellDataset?: Array<number>

  lossFletDataset?: Array<number>
  correctFletDataset?: Array<number>
}

const ChartNeuronLearning: React.FC<propsType> = props => {

  const {
    lossTotalDataset,
    correctTotalDataset,

    lossBuyDataset,
    correctBuyDataset,

    lossSellDataset,
    correctSellDataset,

    lossFletDataset,
    correctFletDataset,
  } = props

  if (!lossTotalDataset) return null
  if (!correctTotalDataset) return null

  if (!lossBuyDataset) return null
  if (!correctBuyDataset) return null

  if (!lossSellDataset) return null
  if (!correctSellDataset) return null

  if (!lossFletDataset) return null
  if (!correctFletDataset) return null

  const labels: Array<number> = []
  for (let i = 0; i < lossTotalDataset.length; i++) {
    labels.push(i*5)
  }

  const accentColor = rainbowColorDict[rainbowColorEnum.dark]
  const noAccentColor = rainbowColorDict[rainbowColorEnum.gray_2]

  const options = {
    responsive: true,
    interaction: {
      mode: 'index' as const,
      intersect: false,
    },
    stacked: false,
    plugins: {
      legend: {
        display: true,
        labels: {
          color: accentColor,
          font: {
            size: 13
          }
        }
      }
    },
    scales: {
      x: {
        grid: {
          display: true,
          drawOnChartArea: false,
          drawTicks: false,
        },
        ticks: {
          color: accentColor,
          maxTicksLimit: 10,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
      y: {
        beginAtZero: false,
        type: 'linear' as const,
        display: true,
        position: 'left' as const,
        grid: {
          color: noAccentColor,
          tickColor: accentColor,
        },
        ticks: {
          color: accentColor,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
      y1: {
        type: 'linear' as const,
        display: true,
        position: 'right' as const,
        grid: {
          drawOnChartArea: false,
          tickColor: accentColor,
        },
        ticks: {
          color: accentColor,
          font: {
            size: 13
          }
        },
        border: {
          color: accentColor
        }
      },
    },
  };

  const data = {
    labels,
    datasets: [
      {
        type: 'line' as const,
        label: 'Loss Total',
        data: lossTotalDataset,
        backgroundColor: 'rgb(0,0,255)',
        borderColor: 'rgb(0,0,255)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'line' as const,
        label: 'Correct Total',
        data: correctTotalDataset,
        backgroundColor: 'rgb(0,191,255)',
        borderColor: 'rgb(0,191,255)',
        yAxisID: 'y1',
        pointRadius: 0,
      },

      {
        type: 'line' as const,
        label: 'Loss Buy',
        data: lossBuyDataset,
        backgroundColor: 'rgb(0,128,0)',
        borderColor: 'rgb(0,128,0)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'line' as const,
        label: 'Correct Buy',
        data: correctBuyDataset,
        backgroundColor: 'rgb(144,238,144)',
        borderColor: 'rgb(144,238,144)',
        yAxisID: 'y1',
        pointRadius: 0,
      },

      {
        type: 'line' as const,
        label: 'Loss Sell',
        data: lossSellDataset,
        backgroundColor: 'rgb(255,0,0)',
        borderColor: 'rgb(255,0,0)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'line' as const,
        label: 'Correct Sell',
        data: correctSellDataset,
        backgroundColor: 'rgb(255,99,71)',
        borderColor: 'rgb(255,99,71)',
        yAxisID: 'y1',
        pointRadius: 0,
      },

      {
        type: 'line' as const,
        label: 'Loss Flet',
        data: lossFletDataset,
        backgroundColor: 'rgb(50,50,50)',
        borderColor: 'rgb(50,50,50)',
        yAxisID: 'y',
        pointRadius: 0,
      },
      {
        type: 'line' as const,
        label: 'Correct Flet',
        data: correctFletDataset,
        backgroundColor: 'rgb(150,150,150)',
        borderColor: 'rgb(150,150,150)',
        yAxisID: 'y1',
        pointRadius: 0,
      },
    ],
  };

  return (
    <Chart type='bar' data={data} options={options}/>
  )
}

export {ChartNeuronLearning}