import {useLocation} from "react-router-dom"
import s from "./activeMenu.module.css"
import {SignalsActiveMenu} from "../signalsActiveMenu/SignalsActiveMenu";
import {SignalsTraderActiveMenu} from "../signalsTraderActiveMenu/SignalsTraderActiveMenu";
import {NeuronLearningActiveMenu} from "../neuronLearningActiveMenu/NeuronLearningActiveMenu";

const ActiveMenu = () => {

  const location = useLocation()

  switch (location.pathname) {
    case "/signals": {
      return <div className={s.wrapper}><SignalsActiveMenu/></div>
    }
    case "/signals_trader": {
      return <div className={s.wrapper}><SignalsTraderActiveMenu/></div>
    }
    case "/neurons": {
      return <div className={s.wrapper}><NeuronLearningActiveMenu/></div>
    }
    default:
      return null
  }
}

export {ActiveMenu}
