import React from "react";
import s from "./gridPriceList.module.css"
import {
  directionBackgroundDict,
  directionEnum,
  rainbowColorDict,
  rainbowColorEnum
} from "../../static/static";
import {hexToRgb} from "../../utils/utils";

type propsType = {
  grid_price_list: number[]
  direction: directionEnum

}

const GridPriceList: React.FC<propsType> = props => {

  const {
    grid_price_list,
    direction,
  } = props

  let color = directionBackgroundDict[direction]

  const prices = grid_price_list.map((item, idx) => {
    const colorRgb = hexToRgb(rainbowColorDict[color as rainbowColorEnum])
    return (<div
      key={idx}
      style={{
        order: 100 - idx,
        gridColumn: Math.trunc(idx / 10) + 1,
        background: `rgba(${[...colorRgb]}, ${item})`,
      }}
    >
      {item}
    </div>)
  })

  return (
    <div
      className={s.container}
    >
      {prices}
    </div>
  )
}

export {GridPriceList}
