import React from "react";
import DatePicker from "react-datepicker";
import s from "./customDatePicker.module.css"

type propsType = {
  label?: string
  selected: Date | null
  onChange: (value: Date | null) => void
}

const CustomDatePicker: React.FC<propsType> = props => {
  const {
    label,
    selected,
    onChange,
  } = props

  return (
    <div className={s.datepicker}>
      <div>{label}</div>
      <DatePicker
        className={s.item}
        selected={selected}
        onChange={onChange}
        withPortal
        calendarStartDay={1}
        isClearable
      />
    </div>
  );
}

export {CustomDatePicker}
