import React from "react";
import { directionEnum, rainbowColorEnum } from "../../static/static";
import { Button } from "../button/Button";
import { adminApi } from "../../api/api";

type propsType = {
  signalUuidToApproveList: string[]
  signalUuidToFletList: string[]
}

const SignalReportBulkHandler: React.FC<propsType> = props => {
  const {
    signalUuidToApproveList,
    signalUuidToFletList,
  } = props

  const changeApprovedValue = async () => {
    try {
      await adminApi.changeApproveValueForSignalByUUIDList(signalUuidToApproveList, true)
    } catch (e: any) {
      console.log(e.message)
    }
  }

  const changeDirectionValue = async () => {
    try {
      await adminApi.changeDirectionValueForSignalByUUIDList(signalUuidToFletList, directionEnum.FLET)
    } catch (e: any) {
      console.log(e.message)
    }
  }

  return (
    <>
      <Button
        label={`Move ${signalUuidToFletList.length} Signals to Flet`}
        clickHandler={changeDirectionValue}
        background={rainbowColorEnum.white}
        color={rainbowColorEnum.dark}
      />
      <Button
        label={`Move ${signalUuidToApproveList.length} Signals to Approved`}
        clickHandler={changeApprovedValue}
        background={rainbowColorEnum.dark}
        color={rainbowColorEnum.green}
      />
    </>
  )
}

export {SignalReportBulkHandler}
